
  // code splitting:
  // https://reacttraining.com/react-router/web/guides/code-splitting

  import React from 'react';
  import { Bundle } from './components';

    import account from './modules/account/ducks';
    import loadAccount from 'bundle-loader?lazy&name=account!./modules/account';
    const Account = (props) => (
      <Bundle load={loadAccount}>
        {(Compo) => <Compo {...props} />}
      </Bundle>
    );
  
    import cashier from './modules/cashier/ducks';
    import loadCashier from 'bundle-loader?lazy&name=cashier!./modules/cashier';
    const Cashier = (props) => (
      <Bundle load={loadCashier}>
        {(Compo) => <Compo {...props} />}
      </Bundle>
    );
  
    import example from './modules/example/ducks';
    import loadExample from 'bundle-loader?lazy&name=example!./modules/example';
    const Example = (props) => (
      <Bundle load={loadExample}>
        {(Compo) => <Compo {...props} />}
      </Bundle>
    );
  
    import guide from './modules/guide/ducks';
    import loadGuide from 'bundle-loader?lazy&name=guide!./modules/guide';
    const Guide = (props) => (
      <Bundle load={loadGuide}>
        {(Compo) => <Compo {...props} />}
      </Bundle>
    );
  
    import home from './modules/home/ducks';
    import homeRoutes from './modules/home';
  
  const routes = [
    ...homeRoutes,
    { path: '/account', component: Account },
{ path: '/cashier', component: Cashier },
{ path: '/example', component: Example },
{ path: '/guide', component: Guide },
  ];

  const reducers = { home, account, cashier, example, guide };

  export {
    routes,
    reducers,
  };



